import { Injectable } from '@angular/core';
import {AngularFirestore, AngularFirestoreCollection, AngularFirestoreDocument} from 'angularfire2/firestore';
import {Observable} from 'rxjs/Observable';
import {Cars} from '../models/Cars';
@Injectable()
export class CarsService {
carsCollection: AngularFirestoreCollection<Cars>;
carsDocument: AngularFirestoreDocument<Cars>;
cars: Observable<Cars[]>;
car: Observable<Cars[]>;

  constructor(private afs: AngularFirestore) {
    this.carsCollection = afs.collection('cars',
      ref => ref.orderBy('carName', 'asc'));
  }
  getCars (): Observable<Cars[]> {
    this.cars = this.carsCollection.snapshotChanges().
      map(changes => {
        return changes.map(action => {
          const data = action.payload.doc.data() as Cars;
          data.id = action.payload.doc.id;
          return data;
        });
      });
    return this.cars;
  }

}



